html,
body {
  background: white;
  margin: 0;
  padding: 0;
  border: 0;
  font-family: 'Lato', sans-serif;
}

.flex-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-height: 95vh;
}

.app {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

.container {
  position: relative;
  width: 100%;
}

.game-container {
  width: 550px;
  height: 550px;
  background: #47494c;
  border-radius: 50%;
  position: relative;
  top: 10vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  border: 2px solid black;
}

.option-container {
  background: #FFFFFF;
