  height: 250px;
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
  text-align: center;
  border: 2px solid black;
  overflow: hidden;
  z-index: 10;
}

.game-btn {
  position: absolute;
  width: 40%;
  height: 40%;
  border: 1px solid black;
}
.game-btn:hover {
  cursor: pointer;
}

.green {
  background-color: #00A74A;
  left: 5%;
  bottom: 55%;
  border-top-left-radius: 100%;
}

.red {
  background-color: #9F0F17;
  left: 55%;
  bottom: 55%;
  border-top-right-radius: 100%;
}

.yellow {
  background-color: #CCA707;
  left: 5%;
  top: 55%;
  border-bottom-left-radius: 100%;
}

.blue {
  background-color: #094A8F;
  left: 55%;
  top: 55%;
  border-bottom-right-radius: 100%;
}

.display {
  width: 70px;
  height: 40px;
  background: #232933;
  border: 2px solid black;
  border-radius: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 25px;
  font-family: 'Orbitron', sans-serif;
  color: #b21108;
}

.row-container {
  margin-top: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 25px;
  padding-right: 25px;
}

.row-container-two {
  margin-top: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 10px;
  padding-right: 10px;
}

.btn {
  padding: 0;
  margin: 0;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  border: 2px solid black;
}
.btn:hover {
  cursor: pointer;
}

.on-btn {
  background: red;
}

.hard-btn {
  background: blue;
}

.restart-btn {
  background: yellow;
}

.btn-container {
  width: 80px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

h3 {
  margin-top: 15px;
  font-size: 40px;
  text-transform: uppercase;
}

.small {
  font-size: 16px;
}

.led {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: black;
  border: 2px solid black;
}

.led-active-on {
  background: red;
}

.led-strict-on {
  background: blue;
}

.footer {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.illuminated-green {
  background: #13ff7c;
}

.illuminated-red {
  background-color: #ff4c4c;
}

.illuminated-blue {
  background-color: #1c8cff;
}

.illuminated-yellow {
  background-color: #fed93f;
}

.gameOver-menu {
