  margin-right: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

h4 {
  margin-top: 30px;
}

/* For mobile: */
@media only screen and (max-width: 550px) {
  .game-container {
    width: 300px;
    height: 300px;
    top: 5vh;
  }

  .option-container {
    top: 155%;
  }
}



